import React from "react";
import Card from "./Card";
const FavCardList = ({ users, fav, deleteUser }) => {
  return (
    <div className="row">
      {users
        .filter((user) => user.fav === true)
        .map((user) => (
          <Card key={user.id} user={user} fav={fav} deleteUser={deleteUser} />
        ))}
    </div>
  );
};

export default FavCardList;
