import React from "react";
import Card from "./Card";
const CardList = ({ fav, users, deleteUser }) => {
  return (
    <div className="row">
      {users.map((user) => (
        <Card key={user.id} user={user} fav={fav} deleteUser={deleteUser} />
      ))}
    </div>
  );
};

export default CardList;
