import React, { Component } from "react";
import axios from "axios";
class Input extends Component {
  state = {
    name: "",
    email: "",
    username: "",
    adduser: false,
    check: false,
  };
  addUserHandler = () => {
    const emailCheck = this.state.email.toLocaleLowerCase();
    const user = {
      name: this.state.name,
      email: emailCheck,
      username: this.state.username,
      fav: false,
    };
    if (user.name && user.email && user.username !== "") {
      if (
        this.props.users.filter((user) =>
          user.email.toLocaleLowerCase().includes(emailCheck)
        ).length === 0
      ) {
        axios
          .post(
            "https://habib-app-default-rtdb.firebaseio.com/habib.json",
            user
          )
          .then((response) => {
            this.props.addUser({ id: response.data.name, ...user });
            this.setState({
              adduser: false,
              name: "",
              email: "",
              username: "",
            });
          });
      } else {
        this.setState({ check: true });
      }
    }
  };
  render() {
    return (
      <React.Fragment>
        {this.state.adduser ? (
          <div>
            <h3>User form</h3>
            {this.state.check ? (
              <p style={{ color: "red" }}>Email already exists!</p>
            ) : null}
            <label>Name</label>
            <input
              type="text"
              className="form-control"
              onChange={(event) => this.setState({ name: event.target.value })}
            />
            <label>Email</label>
            <input
              type="email"
              className="form-control"
              onChange={(event) =>
                this.setState({ email: event.target.value, check: false })
              }
            />
            <label>Username</label>
            <input
              type="text"
              className="form-control"
              onChange={(event) =>
                this.setState({ username: event.target.value })
              }
            />
            <br />
            <button className="btn btn-success" onClick={this.addUserHandler}>
              Add
            </button>
            &nbsp;
            <button
              className="btn btn-danger"
              onClick={() => this.setState({ adduser: false })}
            >
              Cancel
            </button>
          </div>
        ) : (
          <button
            className="btn btn-success"
            onClick={() => this.setState({ adduser: true })}
          >
            Add User
          </button>
        )}
      </React.Fragment>
    );
  }
}

export default Input;
