import React, { useState, useEffect, useContext } from "react";
import Input from "./Input";
import axios from "axios";
import "./cmp.css";
import { UserContext } from "../context/context";
import CardList from "./CardList";
import FavCardList from "./FavCardList";
const Hooks = (props) => {
  const [favUsers, setFavUsers] = useState(false);
  const [search, setSearch] = useState("");
  const userContext = useContext(UserContext);

  useEffect(() => {
    axios
      .get("https://habib-app-default-rtdb.firebaseio.com/habib.json")
      .then((response) => {
        const users = [];
        for (let key in response.data) {
          users.push({ id: key, ...response.data[key] });
        }
        userContext.set_users(users);
      });
  }, []);

  const favUserHandler = (id, value) => {
    userContext.fav(id);
    axios
      .put(
        `https://habib-app-default-rtdb.firebaseio.com/habib/${id}/fav.json`,
        `${value}`
      )
      .then((response) => console.log(response));
  };
  const deleteUserHandler = (id) => {
    userContext.delete(id);
    axios
      .delete(`https://habib-app-default-rtdb.firebaseio.com/habib/${id}.json`)
      .then((response) => {});
  };
  const users = userContext.users.filter((user) =>
    user.name.toLowerCase().includes(search.toLocaleLowerCase())
  );
  return (
    <div className="container">
      <div className="row">
        <div className="col-sm-3">
          <h1> </h1>
          <Input
            users={userContext.users}
            addUser={(user) => userContext.add_users(user)}
          />
          <hr />
        </div>
        <div className="col-sm-9">
          <h2 align="center">Users</h2>
          <div className="row">
            <div className="col-sm-1"></div>
            <div className="col-sm-5" style={{ marginBottom: "10px" }}>
              <button
                className="btn btn-primary"
                onClick={() => setFavUsers(false)}
              >
                All users
              </button>
              &nbsp;&nbsp;
              <button
                className="btn btn-primary"
                onClick={() => setFavUsers(true)}
              >
                Favorite users
              </button>
            </div>
            <div className="col-sm-4">
              <input
                className="form-control"
                onChange={(e) => setSearch(e.target.value)}
                placeholder="Search User"
              />
            </div>
          </div>

          {favUsers ? (
            <FavCardList
              fav={(id, value) => favUserHandler(id, value)}
              users={users}
              delete={deleteUserHandler}
            />
          ) : (
            <CardList
              fav={(id, value) => favUserHandler(id, value)}
              users={users}
              deleteUser={deleteUserHandler}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default Hooks;
